<template>
  <div class="list-item">
    <div class="order-content">
      <div class="date-created">
        {{ setDate(item.date_created) }}-id:{{ item.id }}
      </div>
      <div class="user">{{ item.content.brand }}-userid: {{ item.user }}</div>
      <div class="identity">Provider</div>
      <div class="version">V{{ item.version }}</div>
      <div class="create-reason">{{ item.create_reason }}</div>
      <div class="audit-status">
        <div class="status">{{ setStatus(item.status) }}</div>
        <div class="check" @click="viewDetailsClick" title="点击查看详情">
          {{ operationText }}
        </div>
      </div>
      <div class="audit-operation" v-show="item.status === 3200">
        <div title="填写通过原因" @click="passClick">通过</div>
        <div title="填写拒绝理由" @click="rejectClick">拒绝</div>
      </div>
      <div class="audit-operation" v-show="item.status === 3300">
        <div style="cursor: default">已通过</div>
      </div>
      <div class="audit-operation" v-show="item.status === 3400">
        <div style="cursor: default">已拒绝</div>
      </div>
    </div>
    <div class="remark-content">
      <p class="placeHolder1" />
      <p class="remark">备注：{{ item.review_comment }}</p>
      <p class="placeHolder2" />
    </div>
  </div>
</template>

<script>
import { UrlDecode, processDate } from "../../../../../common/utils";

export default {
  name: "e-ListItem",
  props: {
    item: {
      type: Object,
      default() {
        return {};
      },
    },
    index: {
      type: Number,
    },
    operationText: {
      type: String,
      default: "查看",
    },
  },
  data() {
    return {};
  },
  created() {
    console.log(this.item);
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    viewDetailsClick(e) {
      console.log("before emit this:");
      console.log(this);
      const param = {
        x: e.clientX,
        y: e.clientY,
        index: this.index,
      };
      console.log("message", param);
      this.$bus.$emit("viewDetailsClick", param);
    },
    setStatus(code) {
      if (code === 3200) {
        return "待审核";
      } else if (code === 3300) {
        return "已通过";
      }
      //3400
      else {
        return "已拒绝";
      }
    },
    setModuleName(code) {
      if (code === 1200) {
        return "热门类别";
      }
    },
    setDate(text) {
      return processDate(text);
    },
    rejectClick() {
      this.$bus.$emit("rejectClick", this.index);
      this.$bus.$emit("passProduct", this.item.id);
    },
    passClick() {
      console.log("list item emit pass click:index : ", this.index);
      console.log(this.item.id);
      this.$bus.$emit("passClick", this.index);
      this.$bus.$emit("passProduct", this.item.id);
    },
  },
  components: {},
};
</script>

